import { Box, Container, Divider, Typography, Button } from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import CancelIcon from "@mui/icons-material/Cancel";
import RemoveCircleIcon from "@mui/icons-material/RemoveCircle";
import CircleIcon from "@mui/icons-material/Circle";

export function PreguntaRealizada({ pregunta, auditoriaRealizada }) {
    const respuesta = auditoriaRealizada.respuestas.find(
        (respuesta) => respuesta.id === pregunta.id
    );
    let icon = <CircleIcon />;
    let backgroundColor = "#e0e0e0";
    switch (respuesta.respuesta) {
        case 1:
            icon = <CheckCircleIcon />;
            backgroundColor = "#8dc63f";
            break;
        case 2:
            icon = <CancelIcon />;
            backgroundColor = "#cc4141";
            break;
        case 3:
            icon = <RemoveCircleIcon />;
            backgroundColor = "#f7941d";
            break;
        default:
            break;
    }

    return (
        <Box
            key={pregunta.id}
            sx={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "start",
                alignItems: "start",
            }}
        >
            {pregunta.tipo_id === 1 && (
                <Container
                    sx={{
                        p: 2,
                    }}
                >
                    <Typography variant="body1" sx={{ fontWeight: "bold" }}>
                        {pregunta.pregunta}
                    </Typography>
                    <Divider />
                </Container>
            )}
            {pregunta.tipo_id === 2 && (
                <Container
                    sx={{
                        p: 2,
                    }}
                >
                    <Typography variant="body1">{pregunta.pregunta}</Typography>
                    {pregunta.referencia && (
                        <Typography
                            variant="body2"
                            sx={{ fontStyle: "italic" }}
                        >
                            <b>Referencia:</b> <em>{pregunta.referencia}</em>
                        </Typography>
                    )}
                    <Button
                        variant="contained"
                        fullWidth
                        startIcon={icon}
                        sx={{
                            color: "white",
                            backgroundColor: backgroundColor,
                            mb: 2,
                        }}
                    ></Button>
                    <Typography variant="body2" sx={{ mb: 2 }}>
                        <b>Observaciones:</b>{" "}
                        {respuesta.observaciones || "Sin observaciones"}
                    </Typography>
                    <Divider />
                </Container>
            )}
        </Box>
    );
}
